import React, { Component } from 'react'

import { Input, Button, Row, Col } from 'antd'
import { DeleteOutlined } from '@ant-design/icons'
const { TextArea } = Input

class QTableItem extends Component {
    constructor(props) {
        super(props)
        this.valueChange = this.valueChange.bind(this)
        this.onDelete = this.onDelete.bind(this)
    }
    valueChange(event) {
        let value = event.target.value
        let index = this.props.index
        this.props.onUpdate(value, index)
    }

    onDelete() {
        let index = this.props.index
        this.props.onDelete(index)
    }

    render() {
        let { listInfo } = this.props
        // log('listInfo', listInfo)
        return (
            <Row gutter={20}>
                <Col span={18}>
                    <TextArea
                        value={listInfo.value}
                        onChange={this.valueChange}
                        placeholder={listInfo.label}
                        autoSize={{ minRows: 2 }}
                    />
                </Col>
                <Col span={6} className="delete-item">
                    <Button
                        type="primary"
                        shape="circle"
                        danger
                        icon={<DeleteOutlined />}
                        onClick={this.onDelete}
                    />
                </Col>
            </Row>
        )
    }
}

export default QTableItem
