import React, { Component } from 'react'
import { Button, Form, Row, Col, Space, Input } from 'antd'
import log from '../utils/log'
import QTableItem from '@/components/QTableItem'
import AddItem from '@/components/AddItem'

import local from '@/utils/local'

import '@/styles/Qtable.scss'

// 使用 sortablejs
import Sortable from 'sortablejs'

class Qtable extends Component {
    constructor(props) {
        super(props)
        let list = JSON.parse(local.get('qTable'))
        if (list) {
            this.state = {
                list: list,
            }
        } else {
            this.state = {
                list: [
                    { label: '标题', value: '' },
                    { label: '目的', value: '' },
                    { label: '想法和思路', value: '' },
                    { label: '问题描述', value: '' },
                    { label: '实现代码', value: '' },
                ],
            }
        }
        this.onUpdate = this.onUpdate.bind(this)
        this.onCopy = this.onCopy.bind(this)
        this.onAdd = this.onAdd.bind(this)
        this.onReset = this.onReset.bind(this)
        this.onDelete = this.onDelete.bind(this)
    }
    // 修改列表项的值
    onUpdate(value, index) {
        let list = this.state.list
        list[index].value = value
        this.setState({
            list: list,
        })
        // 保存到 localstorage
        // local.set('qTable', JSON.stringify(list))
    }
    // 删除一项列表项
    onDelete(index) {
        let list = this.state.list
        list.splice(index, 1)
        this.setState({
            list,
        })
        // 保存到 localstorage
        // local.set('qTable', JSON.stringify(list))
    }
    onAdd(title) {
        let list = this.state.list
        list.push({ label: title, value: '' })
        this.setState({
            list: list,
        })
        // 保存到 localstorage
        // local.set('qTable', JSON.stringify(list))
    }

    clipBoard = (text) => {
        let transfer = document.createElement('textarea')
        document.body.appendChild(transfer)
        transfer.value = text
        transfer.focus()
        transfer.select()
        if (document.execCommand('copy')) {
            document.execCommand('copy')
        }
        transfer.blur()

        document.body.removeChild(transfer)
    }
    // 复制到剪贴板
    onCopy() {
        let list = this.state.list
        let text = list.reduce((acc, cur) => {
            let t = `${cur.label}:\r\n    ${cur.value}\r\n`
            acc = acc + t
            return acc
        }, '')

        this.clipBoard(text)
    }

    // 重置表单
    onReset() {
        let list = this.state.list.map((item) => {
            item.value = ''
            return item
        })
        this.setState({
            list: list,
        })
        // 保存到 localstorage
        // local.set('qTable', JSON.stringify(list))
    }

    componentDidMount() {
        // 处理
        const table = document.querySelector('#id-form-items')
        new Sortable(table, {
            onEnd: (evt) => {
                // const targetRow = this.list.splice(evt.oldIndex, 1)[0]
                // this.list.splice(evt.newIndex, 0, targetRow)
                let l = this.state.list
                const row = l.splice(evt.oldIndex, 1)[0]
                l.splice(evt.newIndex, 0, row)
                this.setState({
                    list: l,
                })
            },
        })
    }

    render() {
        let { list } = this.state
        // log('list', list)
        const layout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 20 },
        }
        const tailLayout = {
            wrapperCol: { offset: 4, span: 20 },
        }
        // 每当 list 发生变化, 就保存到 localStorage 里面
        // 保存到 localstorage
        local.set('qTable', JSON.stringify(list))

        return (
            <div className="qtable-container">
                <Row>
                    <Col span={20} offset={4}>
                        <h3>提问表格</h3>
                    </Col>
                </Row>
                <Form name="id-form-table" {...layout} onFinish={this.onCopy}>
                    <Form.Item name="addItem" label="添加项">
                        <AddItem onAdd={this.onAdd} />
                    </Form.Item>
                    <div id="id-form-items">
                        {list.map((item, i) => (
                            <Form.Item
                                name={item.label}
                                label={item.label}
                                key={item.label}
                            >
                                <QTableItem
                                    listInfo={item}
                                    index={i}
                                    onUpdate={this.onUpdate}
                                    onDelete={this.onDelete}
                                />
                            </Form.Item>
                        ))}
                    </div>
                    <Form.Item {...tailLayout}>
                        <Space>
                            <Button type="primary" htmlType="submit">
                                复制到剪贴板
                            </Button>
                            <Button onClick={this.onReset}>重置</Button>
                        </Space>
                    </Form.Item>
                </Form>
            </div>
        )
    }
}

export default Qtable
